import React from "react";
import Data from "../jsons/privilege.json";

const Privilege = (props) => {
  const num = props.resistNum;

  return (
    <div>
      <h4>流派特典</h4>
      <div>
        流派忍法数の上昇：{Data[num].quantity_up}（成長回数：
        {Data[num].growth_quantity}）
      </div>
      <div>流派の背景：{Data[num].background}</div>
      <div>成長限界：{Data[num].sikake}</div>
    </div>
  );
};

export default Privilege;
