import React from "react";
import Data from "../jsons/growth.json";

const Growth = (props) => {
  const num = props.resistNum;

  return (
    <div>
      <h4>成長</h4>
      <div>流派レベル：{Data[num].ryuhaLv}</div>
      <div>流派忍法数：{Data[num].ninpo_quantity}</div>
      <div>成長限界：{Data[num].limit}</div>
      <div>流派功績点：{Data[num].point}</div>
    </div>
  );
};

export default Growth;
