import React, { useState } from "react";
import Footer from "./common_page/Footer";
import Growth from "./components/Growth";
import Ninpo_list from "./components/Ninpo_list";
import Others from "./components/Others";
import Overview from "./components/Overview";
import Privilege from "./components/Privilege";
import "./styles.css";

const componentArray = [
  <Overview />,
  <Growth />,
  <Privilege />,
  <Ninpo_list />,
  <Others />
];

export default function App() {
  const [ryuhaNum, setRyuhaNum] = useState(0);
  const [selectedName, setSelectedName] = useState(0);
  const ryuhaNameArray = ["鍔眼流"];

  const selectedString = ryuhaNameArray[0];

  return (
    <div>
      <h3>独自流派シート</h3>
      <hr />
      <select onChange={(e) => setRyuhaNum(e.target.value)}>
        {ryuhaNameArray.map((ryuhaName, i) => {
          return (
            <>
              {ryuhaName === selectedString ? (
                <option value={ryuhaName} selected>
                  {ryuhaName}
                </option>
              ) : (
                <option value={i}>{ryuhaName}</option>
              )}
            </>
          );
        })}
      </select>
      <hr />
      <Overview resistNum={ryuhaNum} />
      <hr />
      <Growth resistNum={ryuhaNum} />
      <hr />
      <Privilege resistNum={ryuhaNum} />
      <hr />
      <Ninpo_list resistNum={ryuhaNum} />
      <hr />
      <Others resistNum={ryuhaNum} />
      <hr />
      <Footer />
    </div>
  );
}
