import React from "react";
import Data from "../jsons/ninpo_list.json";

const Ninpo_list = (props) => {
  const num = props.resistNum;

  return (
    <div>
      <h4>流派忍法リスト</h4>
      <table className="table" style={{ minWidth: 100 }}>
        <thead>
          <tr>
            <th>忍法名</th>
            <th>類型名</th>
            <th>タイプ</th>
            <th>間合</th>
            <th>コスト</th>
            <th>指定特技</th>
            <th>効果</th>
          </tr>
        </thead>
        <tbody>
          {Data[0].map((ninpo, i) => {
            return (
              <tr>
                <td>{ninpo.ninpo_name}</td>
                <td>{ninpo.type_name}</td>
                <td>{ninpo.kind}</td>
                <td>{ninpo.range}</td>
                <td>{ninpo.cost}</td>
                <td>{ninpo.skill}</td>
                <td>{ninpo.effect}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default Ninpo_list;
