import React from "react";
import Data from "../jsons/overview.json";

const Overview = (props) => {
  const num = props.resistNum;

  return (
    <div>
      <h4>概要</h4>
      <div>管理人：{Data[num].kanrinin}</div>
      <div>流派名：{Data[num].ryuhamei}</div>
      <div>条件：{Data[num].zyoken}</div>
      <div>流儀：{Data[num].ryugi}</div>
      <div>仇敵：{Data[num].kyuteki}</div>
      <div>首魁：{Data[num].shukai}</div>
      <div>構成員：{Data[num].member}</div>
    </div>
  );
};

export default Overview;
