import React from "react";
import Data from "../jsons/others.json";

const Others = (props) => {
  const num = props.resistNum;

  return (
    <div>
      <h4>そのほか</h4>
      {Data[num].map((textData, i) => {
        return (
          <>
            {textData.text == "" ? <br /> : <div key={i}>{textData.text}</div>}
          </>
        );
      })}
    </div>
  );
};

export default Others;
